.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(219, 208, 208, 0.6);
}

.modal-main {
    position: fixed;
    background: white;
    width: 60%;
    height: 40%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}